<template>
  <div>
    <div id="header">
      <header-nav></header-nav>
    </div>
    <div id="content">
      <div class="clearfix content padding-top-100">
        <el-tabs class="mt20" v-model="activeName">
          <el-tab-pane :label="item.classname" v-for="(item,classindex) in classlist" :key="classindex">
            <div v-for="(child,childindex) in item.childlist" :key="childindex">
              <div class="mt25 title">{{child.classname}}</div>
              <div>
                <div v-for="(childitem,index) in child.childlist" :key="index" class="cu-tag"
                     @click="goSearch(childitem.classname)">
                  {{childitem.classname}}
                </div>
              </div>
            </div>
          </el-tab-pane>
        </el-tabs>
        <div class="mb60">

        </div>
      </div>
    </div>
    <FooterBottom></FooterBottom>
    <Totop></Totop>
  </div>

</template>

<script>
  import HeaderNav from "../components/headerNav";
  import FooterBottom from "../components/footer";
  import Totop from '@/components/totop.vue';
  import {getSort} from '@/api/main'
  import {Message} from "element-ui";

  export default {
    name: "sort",
    components: {
      HeaderNav,
      FooterBottom,
      Totop
    },
    data() {
      return {
        activeName: '',
        osshost: 'http://chihuo-app-test-bucket.oss-cn-shenzhen.aliyuncs.com/',
        classlist: '',
      }

    },
    created() {
      this.getClass();
    },
    methods: {
      getClass() {
        getSort().then(response => {
          let res = response.data;
          if (res.success) {
            this.classlist = res.result;
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      navToDetail(id) {
        this.$router.push({path: "/cookBook", query: {id: id}})
      },
      goSearch(searchkey) {
        if (this.nav == "search") {
          window.reload()
        } else {

        }
        this.$router.push({path: "/search", query: {keyword: searchkey}})
        window.reload()
      }
    }
  }
</script>

<style scoped>

  .title {
    font-size: 20px;
    line-height: 20px;
  }

  .cu-tag {
    margin: 20px 40px 0 0;
    font-size: 15px;
    line-height: 20px;
    border-radius: 6px;
    padding: 0 5px;
    color: #999;
    border: 1px solid #eee;
    cursor: pointer;
  }
</style>



